import React from 'react';
import './css/Body.css';
import Win9x from './img/win95-98/windows.png';
import Win31 from './img/win3_1/windows.png';
import Win881 from './img/Win8-8_1/windows.png';
import winME2000 from './img/winME-2000/windows.png';
import WinV7 from './img/WinVista-7/windows.png';
import WinXP from './img/WinXP/windows.png';
import Win10 from './img/win10/windows.png';


class Body extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      catType: 'none',
    }

    this.state = {
      dogType: 'Windows 10',
    }

    this.state = {
      birdType: 'none',
    }
  }

  changeModel() {
    var ele = document.getElementById('button').value;
    this.setState({ catType: ele });

  }

  windowsModel(){
    var alo = document.getElementById('type').value;
    this.setState({ dogType: alo });
  }

  changelErrorVersion(){

  }

  statusChange(){

  }

  windowsSwitch() {

    var win31 = [
      <div>
        <img src={Win31} width="100%" alt="Window" />
      </div>
    ];

    var win881 = [
      <div>
        <img src={Win881} width="100%" alt="Window" />
      </div>
    ];

    var win9x = [
      <div>
        <img src={Win9x} width="100%" alt="Window"/>
      </div>
    ];

    var win10 = [
      <div>
        <img src={Win10} width="100%" alt="Window"/>
      </div>
    ];

    var win2000ME = [
      <div>
        <img src={winME2000} width="100%" alt="Window"/>
      </div>
    ];

    var winXP = [
      <div>
        <img src={WinXP} width="100%" alt="Window"/>
      </div>
    ];

    var winV7 = [
      <div>
        <img src={WinV7} width="100%" alt="Window"/>
      </div>
    ];

    switch (this.state.dogType) {

      case 'Windows 3.1':

        return win31;

      case 'Windows 95/98':

        return win9x;

      case 'Windows ME/2000':

        return win2000ME;

      case 'Windows XP':

        return winXP;

      case 'Windows Vista/7':

        return winV7;

      case 'Windows 8/8.1':

        return win881;

      case 'Windows 10':

        return win10;

      default:

        return win31;

    }

  }

  renderSwitch() {

    var one = [
      <div>
        <div class="form-group">
          <label>botton 1</label><br />
          <input type="text" class="form-control" />
        </div>
      </div>
    ];

    var two = [
      <div>
        <div class="form-group">
          <label>botton 1</label><br />
          <input type="text" class="form-control" />
        </div>

        <div class="form-group">
          <label>botton 2</label><br />
          <input type="text" class="form-control" />
        </div>
      </div>
    ];

    var three = [
      <div>
        <div class="form-group">
          <label>botton 1</label><br />
          <input type="text" class="form-control" />
        </div>

        <div class="form-group">
          <label>botton 2</label><br />
          <input type="text" class="form-control" />
        </div>

        <div class="form-group">
          <label>botton 3</label><br />
          <input type="text" class="form-control" />
        </div>
      </div>
    ];

    var four = [
      <div>
        <div class="form-group">
          <label>botton 1</label><br />
          <input type="text" class="form-control" />
        </div>

        <div class="form-group">
          <label>botton 2</label><br />
          <input type="text" class="form-control" />
        </div>

        <div class="form-group">
          <label>botton 3</label><br />
          <input type="text" class="form-control" />
        </div>

        <div class="form-group">
          <label>botton 4</label><br />
          <input type="text" class="form-control" />
        </div>
      </div>
    ];

    var none = [
      <div>

      </div>
    ];

    switch (this.state.catType) {
      case '1':

        return one;

      case '2':

        return two;

      case '3':

        return three;
      case '4':

        return four;
      case 'none':

        return none;

      default:

        return none;

    }
  }


  render() {

    return (

      <div class="col-md-12 text-center row">
        <div class="col-md-4">

        </div>
        <div class="col-md-4">
          <div>
            {this.windowsSwitch()}
          </div>
          <br /><br />
          <form onSubmit={this.handleSubmit}>
            <div class="form-group">
              <label>version: </label><br />
              <select name="version" class="form-control" id="type" onChange={this.windowsModel.bind(this)}>
                <option value="Windows 3.1">Windows 3.1</option>
                <option value="Windows 95/98">Windows 95/98</option>
                <option value="Windows ME/2000">Windows ME/2000</option>
                <option value="Windows XP">Windows XP</option>
                <option value="Windows Vista/7">Windows Vista/7</option>
                <option value="Windows 8/8.1">Windows 8/8.1</option>
                <option value="Windows 10">Windows 10</option>
              </select>
            </div>

            <div class="form-group">
              <label>type: </label><br />
              <select name="type" id="type" class="form-control">
                <option value="critical">critical</option>
                <option value="alert">alert</option>
                <option value="answer">answer</option>
                <option value="exclamation">exclamation</option>
                <option value="none">None</option>
              </select>
            </div>

            <div class="form-group">
              <label>Title message: </label><br />
              <input type="text" class="form-control" />
            </div>

            <div class="form-group">
              <label>Message: </label><br />
              <input type="text" class="form-control" />
            </div>

            <div class="form-group">
              <label>Numero botton </label><br />
              <select class="form-control" id="button" onChange={this.changeModel.bind(this)}>
                <option value="none">None</option>
                <option value="1">1</option>
                <option value="2">2</option>
                <option value="3">3</option>
                <option value="4">4</option>
              </select>
            </div>

            <div id="button_number">
              {this.renderSwitch()}
            </div>

            <input type="button" value="generate" class="btn btn-primary" />

          </form>
        </div>
        <div class="col-md-4">

        </div>
      </div>
    );
  }
}

export default Body;